<template>
  <v-container id="dropdown-example">
    <v-row>
      <v-col cols="12" sm="1">
        <v-overflow-btn
          v-model="add1"
          class="my-2"
          :items="dropdown_add"
          @change="add_fun(add1)"
          label=""
        ></v-overflow-btn>
      </v-col>
      <v-col cols="12" sm="2">
        <v-overflow-btn
          v-model="add2"
          class="my-2"
          :items="dropdown_add2"
          item-text="address2_name"
          label="2"
          @change="add2_fun(add2)"
        ></v-overflow-btn>
      </v-col>
      <v-col cols="12" sm="2">
        <v-overflow-btn
          v-model="fac"
          class="my-2"
          :items="dropdown_fac"
          item-text="address_name"
          label=""
          @change="fac_fun(fac)"
        ></v-overflow-btn>
      </v-col>

      <v-col cols="12" sm="2">
        <v-overflow-btn
          v-model="cate"
          class="my-2"
          :items="dropdown_category"
          label=""
          @change="cate_fun(cate)"
        ></v-overflow-btn>
      </v-col>

      <v-col cols="12" sm="3">
        <v-text-field
          label=""
          prepend-inner-icon="mdi-magnify"
        ></v-text-field>
      </v-col>
      <v-col cols="12" sm="2">
        <v-btn class="ma-2" outlined color="indigo" @click="select_search">Search</v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios'
  export default {
    data: () => ({
      select_data:{
        add1 : "",
        add2 : "",
        fac : "",
        cate : "",
      },
      dropdown_add: [
        {text:""},
        {text:""},
      ],
      dropdown_add2: [],
      dropdown_fac: [
        "",
        //{ text: ""},
      ],
      dropdown_category: [
        "",
        "",
        "",
        "",
        "",
        "()",
        "",
        "",
